body {
  margin: 0;
  padding: 0;
  font-family: 'Alexandria', sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.timer-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timer-view p {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.red {
  background-color: #ff4c51;
}

.blue {
  background-color: #167bd0;
}

.green {
  background-color: #32ab17;
}

.yellow {
  background-color: #eed10a;
}

.pink {
  background-color: #d25ed1;
}

.white {
  background-color: #e7e7e7;
}

p {
  padding: 0;
  margin: 0;
}

.player-name {
  font-size: 16vmin;
}

.timer {
  font-size: 40vmin;
}

.closeButton {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: none;
  border: none;
}

.version-number {
  font-size: 18px;
}